<template>
  <div class="content">
    <div class="container-fluid">
      <div class="row">
        <div class="col-12">
          <card>
            <h4 slot="header" class="card-title">Edit User</h4>
            <form>
              <div class="row">
                <div class="col-md-6">
                  <label>Select User Role</label>
                  <select v-model="user.role_id" class="form-control">
                    <option value="1">Admin</option>
                  </select>
                  <div v-if="isSubmitted && $v.user.role_id.$error" class="invalid-feedback">
                  <span v-if="!$v.user.role_id.required">Select role is required.</span>
                </div>
                </div>
                <div class="col-md-6">
                  <base-input type="text"
                            label="Full Name"
                            placeholder="Full Name"
                            v-model="user.name">
                  </base-input>
                  <div v-if="isSubmitted && $v.user.name.$error" class="invalid-feedback">
                  <span v-if="!$v.user.name.required">Full name field is required.</span>
                </div>
                </div>
            
              </div>

              <div class="row">
                <div class="col-md-6">
                  <base-input type="email"
                            label="Email"
                            placeholder="Email"
                            v-model="user.email">
                  </base-input>
                  <div v-if="isSubmitted && $v.user.email.$error" class="invalid-feedback">
                  <span v-if="!$v.user.email.required">Email field is required.</span>
                </div>
                </div>
                <div class="col-md-6">
                  <base-input type="password"
                            label="Password"
                            placeholder="Password"
                            v-model="user.password">
                  </base-input>
                    <div v-if="isSubmitted && $v.user.password.$error" class="invalid-feedback">
                    <span v-if="!$v.user.password.required">Password field is required.</span>
                  </div>
                </div>
   
              </div>
              <div class="text-center">
                <button type="submit" class="btn btn-info btn-fill float-right" @click.prevent="saveProfile">
                  Save
                </button>
                <ajax-loader v-if="loading"></ajax-loader>

              </div>
              <div class="clearfix"></div>
            </form>
          </card>
        </div>
      </div>
    </div>
</div>
</template>
<script>
import {
      required,
      email,
  } from "vuelidate/lib/validators";
import Card from 'src/components/Cards/Card.vue'
import AjaxLoader from '../../AjaxLoader.vue';
import axios from "axios";

export default {
  components: {
    Card,
    AjaxLoader
  },
  data () {
    return {
      user: {
        name: '',
        email: '',
        role_id: '',
        password: '',
        password_confirmation:''
      },
      isSubmitted: false,
      loading: false
    }
  },
  validations: {
    user: {
      name:{required},
      email:{required, email},
      password: {required},
      role_id: {required}
    }
  },
  mounted(){
    this._getUser(this.$route.params.id)
  },
  methods: {
    _getUser(id){
            this.loading=true;
            axios.get(process.env.VUE_APP_API_URL+'user-list',{
              headers: {
                  'Content-Type': 'application/json',
                  'Authorization' : 'Bearer '+ localStorage.getItem('token')
              }
            })
            .then(response => {
                if(response.status==200 && response.data.users){
                  this.tableData = response.data.users
                }
                this.loading=false;
            })
            .catch(error => {
              this.$notifications.notify(
              {
                message: `<span>Something went wrong.</span>`,
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'danger'
              })
                this.loading=false;
            }).finally( () => {
                this.loading = false
            })
      },
    saveProfile () {
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
          return;
      }
      this.user.password_confirmation = this.user.password
      this.loading=true;
      axios.post(process.env.VUE_APP_API_URL+'register', this.user, {
          headers: {
              'Authorization' : 'Bearer '+ localStorage.getItem('token')
          },
          })
          .then(response => {
            this.loading=false;
            this.isSubmitted=false;
            console.log(response)
            if(response.status == 200 && response.data){
              this.$notifications.notify(
              {
                message: '<span>New user addedd successfully</span>',
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'success'
              })
              this.$router.push({name: 'User'});
            }else{
              this.$notifications.notify(
              {
                message: '<span>'+response.data.error_message+'</span>',
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'danger'
              })
            }
         
          })
          .catch(error => {
            if(error.response.status == 422){
              this.$notifications.notify(
              {
                message: `<span>`+error.response.data.message+`</span>`,
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'danger'
              })
            }else{
              this.$notifications.notify(
              {
                message: `<span>Something went wrong.</span>`,
                icon: 'nc-icon nc-bell-55',
                horizontalAlign: 'right',
                verticalAlign: 'top',
                type: 'danger'
              })
            }
              this.isSubmitted=false;
              this.loading=false;
          }).finally( () => {
              this.isSubmitted = false
              this.loading=false;
          })
    }
  }
}

</script>
